	module.exports = {"data":{"markdownRemark":{"html":"<p>早朝にドイツの作家が小説を読んで、人として姿を現し、妻と30年間生活している人の話を伝えます。もちろんそれは物語です。</p>\n<p>新しい税制に関する本を読んで、新しい税制の予備的な理解を得てください。新しく追加された付加価値税は重要な税金タイプであり、全国の経済発展と中央財務能力の向上にとって非常に重要です。所得税も分類され、より明確になっています。</p>\n<p>この本の欠点は、それが税制分担制度の重要性を言っておらず、税制分担制度が何であるかも言わないということです。これはこの税制改革の重要な一歩であり、他のすべての税制改革よりも重要な改革であり、中国の将来発展にとって最も基本的な意味を持つ改革です。また、勉強する本を探してください。税制分担制度の下では、中央政府と地方政府との関係にはどんな新しい矛盾があるだろうか。どんな種類の改革も、古い矛盾を解決しながら常に新しい矛盾を作り出します。そして人々は新しい矛盾を解決するために熱心に働かなければなりません。社会はこのプロセスで進んでいます。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0039.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0039.md","frontmatter":{"title":"2月22日 火曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0039.md absPath of file >>> MarkdownRemark"}}

